.la-homepage .la-content-wrapper {
  opacity: 1;
  -webkit-transform: scale(1) translateY(0);
  transform: scale(1) translateY(0);
  transition: 300ms -webkit-transform, -webkit-filter ease-in;
  transition: 300ms transform, filter ease-in;
  transition: 300ms transform, filter ease-in, 300ms -webkit-transform, -webkit-filter ease-in;
}

.la-homepage.modal-open {
  background-color: #fff;
}

.la-homepage.modal-open .la-content-wrapper {
  -webkit-transform: scale(0.95) translateY(21px);
  transform: scale(0.95) translateY(21px);
  -webkit-filter: blur(3px);
  filter: blur(3px);
  opacity: .5;
  transition: 500ms -webkit-transform, -webkit-filter ease-in;
  transition: 500ms transform, filter ease-in;
  transition: 500ms transform, filter ease-in, 500ms -webkit-transform, -webkit-filter ease-in;
}

.la-homepage .h1-banner {
  background-color: #fff;
  padding-top: 18px;
  padding-bottom: 50px;
  position: relative;
}

.la-homepage .h1-banner h1 {
  font-family: Roboto;
  font-size: 8vw;
  min-height: 70px;
  margin: 10px 0 1vw;
  font-weight: 100;
  line-height: 120%;
}

.la-homepage .h1-banner h5 {
  font-family: Roboto;
  text-transform: lowercase;
  font-size: 1.4rem;
  margin: 0 0;
  height: 100px;
}

.la-homepage .h1-banner::before {
  will-change: filter, opacity, background-size, background-position;
  opacity: 0;
  -webkit-filter: blur(24px);
  filter: blur(24px);
  content: '';
  height: 325px;
  width: 100%;
  position: absolute;
  bottom: 20px;
  background-image: url("https://cdn.luckyape.com/images/skeletons.jpg");
  background-attachment: scroll;
  background-repeat: repeat-x;
  background-position: -100vw bottom;
  background-size: 2vh auto;
}

.la-homepage .h1-banner.bgFadeIn::before {
  transition: opacity 1000ms, background-size 1000ms, background-position 2000ms cubic-bezier(1, 1.5, 0.51, 1), -webkit-filter 1000ms;
  transition: opacity 1000ms, filter 1000ms, background-size 1000ms, background-position 2000ms cubic-bezier(1, 1.5, 0.51, 1);
  transition: opacity 1000ms, filter 1000ms, background-size 1000ms, background-position 2000ms cubic-bezier(1, 1.5, 0.51, 1), -webkit-filter 1000ms;
  opacity: 1;
  -webkit-filter: blur(0px);
  filter: blur(0px);
  background-size: 360px auto;
  background-position: 48% bottom;
}

@-webkit-keyframes backgroundScrollSM {
  0% {
    background-position: -300% bottom;
  }
}

@keyframes backgroundScrollSM {
  0% {
    background-position: -300% bottom;
  }
}

